// import toDoListItem from './components/toDoListItem.vue'
import Cookies from 'js-cookie'

export default {
  name: 'Home',
  components: {
    // homeMap,
    // dataSourcePie,
    // visiteVolume,
    // serviceRequests,
    // userFlow,
    // inforCard,
    // mapDataTable,
    // toDoListItem
  },
  data () {
    return {
      toDoList: [
        {
          title: '交付工作：网络直播功能交付并做好现场升级支持'
        },
        {
          title: '接口联调：配合IOS终端APP的接口联调测试和修正'
        },
        {
          title: '功能实现：完成活动分地区发布功能的实现和自测'
        },
        {
          title: '功能实现：任意页面基本元素支持图片上传'
        },
        {
          title: '功能实现：首页获取地区接口增加地区图片信息返回'
        }
      ],
      count: {
        createUser: 496,
        visit: 3264,
        collection: 24389305,
        transfer: 39503498
      },
      cityData: cityData,
      showAddNewTodo: false,
      newToDoItemValue: '',
      userName: ''
    }
  },
  computed: {
    avatorPath () {
      return localStorage.avatorImgPath
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.userName = Cookies.get('cloudUser')
    },
    addNewToDoItem () {
      this.showAddNewTodo = true
    },
    addNew () {
      if (this.newToDoItemValue.length !== 0) {
        this.toDoList.unshift({
          title: this.newToDoItemValue
        })
        setTimeout(() => {
          this.newToDoItemValue = ''
        }, 200)
        this.showAddNewTodo = false
      } else {
        this.$Message.error('请输入待办事项内容')
      }
    },
    cancelAdd () {
      this.showAddNewTodo = false
      this.newToDoItemValue = ''
    }
  }
}
</script>
